form{
    display: flex;
    flex-direction: column;
}
input{
    border-top: none;
    border-left: none;
    border-right: none;
    font-size: .8rem;
    padding: 1% 4%;
    color: rgba(0, 0, 0, 0.518);

}
label{
    font-size: .9rem;
    color: rgba(0, 0, 0, 0.518);
}
.step{
    display: flex;
    flex-direction: column;
    padding-bottom: 3%;
}

.submit{
    border: none;
    background-color: rgb(213, 59, 85);
    color: white;
    border-radius: 20px;
    padding: 3% 2%;
    width: 40%;
    height: 45px;
    font-size: .85rem;

}
.submit:hover{
    background-color: rgb(237, 84, 109);
}
a{
    color: rgb(66, 148, 255);
    text-decoration: none;
}
a:hover{
    text-decoration: underline;
}
.bottom{
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    padding: 5% 0;
    align-items: center;
    padding-top: 40px;
    gap: 5%;
}
.step input{
    padding-top: 1%;
    outline: none;
}
.step input:focus{
    padding-top: 3%;
    outline: none;
}
.error{
    color: #ff7979;
    font-size: .8rem;
    text-align: right;
}
.text{
    font-size: .8rem;
}

@media only screen and (max-width: 770px){
    .submit {
      width: 40%;
      font-size: .8rem;
    }
  }