.login{
    padding: 10%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 50%;
    gap: 2%;
}

.btm{
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: center;
    gap: 5%;
}

.txt{
    font-size: .8rem;
}

.sign{
    border: none;
    background-color: rgb(129, 127, 127);
    color: white;
    border-radius: 20px;
    padding: 3% 2%;
    width: 35%;
    height: 45px;
    font-size: .8rem;

}
.sign:hover{
    background-color: rgb(146, 145, 145);
}

input{
    border-top: none;
    border-left: none;
    border-right: none;
    font-size: .8rem;
    padding: 1% 4%;
    color: rgba(0, 0, 0, 0.518);
    width: 93%;

}

.forgot{
    color: rgba(0, 0, 0, 0.518);
    font-size: .8rem;
    text-align: right;
}

.forgot span{
    font-weight: bold;
}

@media only screen and (max-width: 770px){
    .login{
        padding: 7% 10%;
        display: flex;
        flex-direction: column;
    }
    .btm{
    }
}
@media only screen and (max-width: 480px){
    .login{
        height: 60%;
    }
    .head{
        padding: 2% 3%;
        height: 35%;
    }
    .btm{
        padding-top: 10px;
    }
}