.App {
  display: flex;
  flex-direction: column;
  background-color: white;
  padding: 2% 3%;
  width: 20%;
}

h1{
  padding-bottom: 5%;
  color: rgb(213, 59, 85);
  text-align: center;
  margin: 0;
  font-size: 1.8rem;
}

.Apps{
  display: flex;
  flex-direction: column;
  background-color: white;
  width: 26%;
  height: 60%;
}

.head{
  background-image: linear-gradient(to right, rgb(255, 179, 86), rgb(255, 86, 86));
  padding: 2% 3%;
  height: 30%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}

.hd_hd{
  color: white;
  padding-bottom: 1%;
}

.socials{
  padding: 3%;
  width: 30%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.social{
  width: 20%;
}

.ggl{
  width: 25%;
}

@media only screen and (max-width: 770px){
  .App {
    padding: 5%;
    width: 40%;
  }
  h1{
    font-size: 2rem;
  }
  .Apps{
    width: 46%;
    height: 57%;
  }
  .hd_hd{
    padding: 2%;
    font-size: 1.8rem;
  }
  .head{
    height: 30%;
  }
}

@media only screen and (max-width: 480px){
  .App {
    padding: 5% 8%;
    width: 70%;
  }
  .head{
    padding: 0%;
    height: 50%;
  }
  .login{
    height: 50%;
  }
  .Apps{
    width: 90%;
    height: 55%;
    justify-content: space-between;
  }
  .hd_hd{
    padding: 2%;
    font-size: 1.8rem;
  }
  .head{
    height: 25%;
  }
}

